<template>
  <div id="app">
    <div>
      count: {{count}}
      <button @click='add'>+</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      count: 0
    }
  },
  created(){
    // try{
      window.sss();
    // }catch(e){
    //   console.log(e)
    //   throw new Error(e)
    // }
  },
  methods: {
    add () {
      this.count++
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



